 #faq .wp-collapse-open{
    margin-bottom: 30px;
    display: flex;
    justify-content: right;
 }

 #faq .panel-body{
    display: none;
 }

 #collapse1 .panel-body{
    display: block;
 }

 #faq .wp-collapse-open a{
    padding: 5px 10px;
    border: 1px solid #8b8b8b;
    text-decoration: none;
    margin-left: 10px;
 }

 #faq .panel-default{
    margin-bottom: 0.5em;
    background-color: #fff;
 }

 #faq .panel-heading{
    position: relative;
    h4{        
        margin-bottom: 0;
        margin-top: 10px;        
        a{
            border: 1px solid #ddd;
            padding: 10px 40px 10px 15px;
            margin: 0;
            position: relative;
            font-weight: normal;
            display: block;
            text-decoration: none;
            .collapse-icon{
                position: absolute;
                right: 15px;
                width: 20px;
                height: 20px;
                float: right;
                text-align: center;
                top: 5px;
                border-radius: 50%;
            }
        }
    }
}

#faq .panel-heading [data-toggle="collapse"] span.collapse-icon::after{
    font-family: "Font Awesome 5 Free";
    font-weight: 900;
    font-size: 15px;
    content: "ÔÅ∑";
    position: absolute;
    top: 7px;
}

#faq .collapse.in{
    display: block;
}

#faq .panel-collapse{
    height: auto !important;
    padding: 0;
    border: 1px solid #ddd;
    border-top: none;
    .panel-body{
        padding: 10px 15px;
        p{
            margin-top: 0;
            margin-bottom: 10px;
        }
    }
}


.wptravel-block-trip-faqs.second-design .wp-collapse-open a{
    border-radius: 30px;
    font-size: 10px;
 }
 
 .wptravel-block-trip-faqs.second-design .panel-heading [data-toggle=collapse].collapsed span.collapse-icon:after{
    content: "\f055" !important;
 }
 
 .wptravel-block-trip-faqs.second-design .panel-heading [data-toggle=collapse] span.collapse-icon:after{
    content: "\f056" !important;
 }
 
 .wptravel-block-trip-faqs.second-design h4:focus{
    outline: none !important;;
 }
 
 .wptravel-block-trip-faqs.second-design h4 a{
    font-size: 18px !important;
    border: none !important;
    padding: 10px 0px 20px 0px !important;
    outline: none !important;
 }
 
 .wptravel-block-trip-faqs.second-design .panel-collapse{
    border: none !important;
 }
 
 .wptravel-block-trip-faqs.second-design .panel-default{
    background-color: transparent !important;
    margin-bottom: 0px !important;
    border-bottom: 1px solid #ddd !important;
    position: relative !important;
 }
 
 .wptravel-block-trip-faqs.second-design .panel-default:last-child{
    border-bottom: none !important;
 }
 
 .wptravel-block-trip-faqs.second-design h4:hover{
    background-color: transparent !important;
 }
 